nav {
	font: 0.9em $navFont;
	margin: 0;
	//float: left;
	white-space: nowrap;
	position: relative;
	padding-bottom: 20px;
	border-bottom: 2px solid $black-coral;
	width: 100%;
	margin-bottom: 50px;
	margin-left: -20px;
	ul {
		list-style-type: none;
		padding: 0;
		display: inline;
		padding: 20px;
		li {
			$borderRadius: 15px;
			display: inline;

			a {
				color: $maximum-blue-green;
				text-decoration: none;
				transition: color 0.2s;
				margin: 0;
				padding: 15px;
				margin-left: 5%;
				margin-right: 5%;
				font-size: 1.2em;

				&:hover {
					color: $rich-black;
				}
			}
		}
	}
}