$size: 200px;

body {
	background: $ghost-white;
}

.header {
	float: left;
	width: 15%;
	text-align: center;
	border-radius: 7px;
	position: fixed;
	padding: 20px;
	margin-top: 50px;
	margin-left: 5%;
	background: white;
	box-shadow: 0 4px 8px 0 rgba(73, 88, 103, 0.2), 0 6px 20px 0 rgba(73, 88, 103, 0.2);
	max-height: 80%;

	&:hover {
		button {
			box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
			transition: box-shadow 0.2s;
			cursor: pointer;
		}
	}

	h1 {
		display: inline;
		font: 1.7em $headerFont;
		white-space: nowrap;
		color: $black-coral;
	}

	.strapline {
		font: 0.8em $straplineFont;
		color: $straplineColor;
	}

	.profile_picture {
		object-fit: cover;
		width: $size;
		height: $size;
		border-radius: 5%;	
		display: block;
		margin: auto;
		margin-top: 20px;
		margin-bottom: 20px;

	}

	.contact_info {
		$size: 15px;
		color: $navLinksColor;
		width: 80%;
		margin: auto;
		margin-top: 30px;
		margin-bottom: 30px;
		font-family: $contactFont;
		text-align: left;
		white-space: nowrap;

		p {
			margin: 0;
			margin-left: -5px;

			&:before {
				background-size: $size $size;
			    display: inline-block;
			    width: $size; 
			    height: $size;
			    content:"";	
			    margin-right: 4px;
			}
		}

		.phone {
			&:before {
			    background-image: url(assets/images/phone.png);
				
			}
		}

		.email {
			&:before {
			    background-image: url(assets/images/email.png);
				
			}
		}

	}

	button {
		background: $maximum-blue-green;
		border-radius: 5px;
		padding: 10px;
		border: none;
		font: 1em $straplineFont;
		color: white;
		margin: 5px;
		transition: box-shadow 0.2s;
	}
}


